import Vue from 'vue' //Vue
import Router from 'vue-router' //vue-router

import Header from '~/pages/header'
import Footer from '~/pages/footer'
import Index from '~/pages/index' //
import Login from '~/pages/login' //
import Register from '~/pages/register' //
import Forgot from '~/pages/forgot' //
import Quotation from '~/pages/quotation' //
import Trade from '~/pages/trade' //
import Notice from '~/pages/notice' //
import NoticeDetail from '~/pages/notice_detail' //
import Order from '~/pages/order' //
import OrderE from '~/pages/order2' //
import Wallet from '~/pages/wallet' //
import Agr from '~/pages/agr' //


Vue.use(Router)

export default new Router({
	routes: [ //
		{ //
			path: '/', //
			name: 'Index', //
			components: { //
				default: Index,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/login',
			name: 'Login',
			components: {
				default: Login,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/register',
			name: 'Register',
			components: {
				default: Register,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/forget',
			name: 'Forgot',
			components: {
				default: Forgot,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/quotation',
			name: 'Quotation',
			components: {
				default: Quotation,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/trade',
			name: 'Trade',
			components: {
				default: Trade,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/notice',
			name: 'Notice',
			components: {
				default: Notice,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/notice/detail/:id',
			name:'NoticeDetail',
			components: {
				default: NoticeDetail,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/order',
			name:'Order',
			components: {
				default: Order,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/order_entrust',
			name: 'OrderEntrust',
			components: {
				default: OrderE,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/wallet',
			name:'Wallet',
			components: {
				default: Wallet,
				header: Header,
				footer: Footer
			}
		}, {
			path: '/agr',
			name: 'Agr',
			components: {
				default: Agr,
				header: Header,
				footer: Footer
			}
		}
	]
})

const originalPush = Router.prototype.push;
Router.prototype.push = function push(location) {
	return originalPush.call(this, location).catch(err => err)
};



